      const screenX = event.clientX - left;
      const screenY = event.clientY - top;

      const cameraX = camera.position.x;
      const cameraY = camera.position.y;

      const mouseXInWorldBefore = (screenX - width / 2) * sceneScale;
      const mouseYInWorldBefore = -1 * (screenY - height / 2) * sceneScale;

      sceneScale += Math.sign(event.deltaY) * 0.02;
      sceneScale = Math.min(Math.max(0.125, sceneScale), 4);

      const mouseXInWorldAfter = (screenX - width / 2) * sceneScale;
      const mouseYInWorldAfter = -1 * (screenY - height / 2) * sceneScale;

      camera.left = (width * sceneScale) / -2;
      camera.right = (width * sceneScale) / 2;
      camera.top = (height * sceneScale) / 2;
      camera.bottom = (height * sceneScale) / -2;
      camera.updateProjectionMatrix();

      const diffX = mouseXInWorldBefore - mouseXInWorldAfter;
      const diffY = mouseYInWorldBefore - mouseYInWorldAfter;

      camera.position.x = cameraX + diffX;
      camera.position.y = cameraY + diffY;
