class MouseSelection {
  isDragged: boolean = false;
  isDragStarted: boolean = false;
  end: { x: number; y: number } = { x: 0, y: 0 };
  start: { x: number; y: number } = { x: 0, y: 0 };

  onUp() {
    this.isDragStarted = false;
    this.isDragged = false;
    this.start.x = 0;
    this.start.y = 0;
    this.end.x = 0;
    this.end.y = 0;
  }

  onDown(x: number, y: number) {
    this.isDragStarted = true;
    this.start.x = x;
    this.start.y = y;
    this.end.x = x;
    this.end.y = y;
  }

  onMove(x: number, y: number) {
    if (this.isDragStarted == true) {
      this.end.x = x;
      this.end.y = y;
      if (this.isDragged === false) {
        const { start, end } = this;
        const deltaX = end.x - start.x;
        const deltaY = end.y - start.y;

        if (Math.abs(deltaX) > 5 || Math.abs(deltaY) > 5) {
          this.isDragged = true;
        }
      }
    }
  }

  contain(p: { x: number; y: number }) {
    const { start, end } = this;

    const p1_x = Math.min(start.x, end.x);
    const p1_y = Math.min(start.y, end.y);

    const p2_x = Math.max(start.x, end.x);
    const p2_y = Math.max(start.y, end.y);

    return p.x > p1_x && p.x < p2_x && p.y > p1_y && p.y < p2_y;
  }
}

class MouseSelectionHelper {
  element: HTMLDivElement;
  renderer: any;
  startPoint: Vector2;
  pointTopLeft: Vector2;
  pointBottomRight: Vector2;
  isDown: boolean;

  constructor() {
    this.element = document.createElement("div");
    this.element.style.pointerEvents = "none";
    this.element.style.background = "rgba(75, 160, 255, 0.3)";
    this.element.style.border = "1px solid #55aaff";
    this.element.style.position = "absolute";
    this.startPoint = new Vector2();
    this.pointTopLeft = new Vector2();
    this.pointBottomRight = new Vector2();
    this.isDown = false;
  }

  setRenderer(renderer) {
    this.renderer = renderer;
  }

  onDown(screenX: number, screenY: number) {
    this.isDown = true;
    this.renderer.domElement.parentElement.appendChild(this.element);

    this.element.style.left = screenX + "px";
    this.element.style.top = screenY + "px";
    this.element.style.width = "0px";
    this.element.style.height = "0px";

    this.startPoint.x = screenX;
    this.startPoint.y = screenY;
  }

  onMove(screenX: number, screenY: number) {
    if (this.isDown) {
      this.pointBottomRight.x = Math.max(this.startPoint.x, screenX);
      this.pointBottomRight.y = Math.max(this.startPoint.y, screenY);
      this.pointTopLeft.x = Math.min(this.startPoint.x, screenX);
      this.pointTopLeft.y = Math.min(this.startPoint.y, screenY);

      this.element.style.left = this.pointTopLeft.x + "px";
      this.element.style.top = this.pointTopLeft.y + "px";
      this.element.style.width =
        this.pointBottomRight.x - this.pointTopLeft.x + "px";
      this.element.style.height =
        this.pointBottomRight.y - this.pointTopLeft.y + "px";
    }
  }

  onUp() {
    if (this.element.parentElement !== null) {
      this.element.parentElement.removeChild(this.element);
    }
  }

  // this.renderer.domElement.addEventListener( 'mousedown', function ( event ) {

  //   this.isDown = true;
  //   this.onSelectStart( event );

  // }.bind( this ), false );

  // this.renderer.domElement.addEventListener( 'mousemove', function ( event ) {

  //   if ( this.isDown ) {

  //     this.onSelectMove( event );

  //   }

  // }.bind( this ), false );

  // this.renderer.domElement.addEventListener( 'mouseup', function ( event ) {

  //   this.isDown = false;
  //   this.onSelectOver( event );

  // }.bind( this ), false );
}

