import React from "react";
import "bulma/css/bulma.css";
import "./App.css";
import { EditorScene } from "./components/Editor";
import { Provider } from "react-redux";
import { appStore } from "./immerReducers/appStore";

const App: React.FC = () => {
  console.log("render App.tsx");
  
  React.useEffect(() => {
    appStore.dispatch({
      type: "startAppEvent",
      payload: { state: appStore.getState() },
    });
  }, []);

  return (
    <div className="App">
      <Provider store={appStore}>
        <EditorScene />
      </Provider>
    </div>
  );
};

export default App;

