import {
  Editor,
  EditorMode,
  BeadTypes,
  EditorMenuMain,
  EditorGridType,
  BrushType,
} from "../editorObjects/Editor";
import { Reducer } from "redux";
import { buildPoint } from "../editorObjects/Point";
import { composeReducers } from "immer-reducer";
import { createActionCreators, createReducerFunction } from "immer-reducer";
import { DraggableOperationsImmerReducer } from "./DraggableOperationsImmerReducer";
import { PersistImmerReducer } from "./PersistImmerReducer";
import { ChangeEditorModeImmerReducer } from "./ChangeEditorModeImmerReducer";
import { MainImmerReducer } from "./MainImmerReducer";
import { MouseSelectionImmerReducer } from "./MouseSelectionImmerReducer";
import { ToolbarImmerReducer } from "./ToolbarImmerReducer";
import { defaulBeadColor } from "../components/colors";
import { TopbarImmerReducer } from "./TopbarImmerReducer";
import { PaintImmerReducer } from "./PaintImmerReducer";
import { buildBead } from "../editorObjects/Bead";
import { produce } from "immer";
import { getEventEmitter } from "../EventEmitter";

const defaultBead = buildBead({});

const defaultAppData: Editor = {
  appData: {
    beads: {},
    lines: {},
  },
  ui: {
    lineSettings: {
      settings: {
        beadsAutoCount: true,
        beadsAutoSpacing: true,
        beadsCount: 0,
        beadsSpacing: 0,
        defaultBead: {
          type: BeadTypes.BasicEllipse,
          rotate: 0,
          color: defaulBeadColor,
          height: 52,
          width: 52,
          keepAspectRatio: false,
        },
      },
    },
    saveInfo: {
      saved: false,
      haveUnsavedChanges: false,
    },
    selectedLinesIds: {},
    selectedLinesIdsCount: 0,
    selectedBeadsIds: {},
    selectedBeadsIdsCount: 0,
    mouseSelection: {
      start: buildPoint(0, 0),
      end: buildPoint(0, 0),
      isDragStarted: false,
      isDragged: false,
    },
    currentMenuMainOpen: EditorMenuMain.None,
    grid: {
      type: EditorGridType.Hex,
      hexSize: 30,
      snapEnabled: true,
      isVisible: true,
      width: 30,
      height: 30,
    },
    brush: {
      type: BrushType.Color,
      size: 50,
      color: defaulBeadColor,
    },
    background: {
      image: {
        url: "",
        scale: 1,
        position: buildPoint(0, 0),
        opacity: 0.5,
      },
    },
    beadsTemplate: {
      color: defaultBead.color,
      width: defaultBead.width,
      height: defaultBead.height,
      keepAspectRatio: defaultBead.keepAspectRatio,
      rotate: defaultBead.rotate,
      type: defaultBead.type,
    },
    currentBeadChain: {
      isDragEnabled: false,
      beadIds: [],
    },
    sceneWrapperWidth: 0,
    sceneWrapperHeight: 0,
    sceneWrapperPosition: {
      x: 0,
      y: 0,
      angle: 0,
      scale: 0,
      top: 0,
      left: 0,
    },
  },
  mode: EditorMode.Idle,
};

export const Actions = {
  Main: createActionCreators(MainImmerReducer),
  Draggable: createActionCreators(DraggableOperationsImmerReducer),
  Persist: createActionCreators(PersistImmerReducer),
  ChangeEditorMode: createActionCreators(ChangeEditorModeImmerReducer),
  MouseSelection: createActionCreators(MouseSelectionImmerReducer),
  Toolbar: createActionCreators(ToolbarImmerReducer),
  Topbar: createActionCreators(TopbarImmerReducer),
  Paint: createActionCreators(PaintImmerReducer),
};

const eventEmitter = getEventEmitter();

const byId = produce((state, action) => {
  console.log("reducer... ", action.type);
  eventEmitter.emit(action.type, action.payload, state);
}, {});

export const store = composeReducers(
  createReducerFunction(MainImmerReducer, defaultAppData),
  createReducerFunction(DraggableOperationsImmerReducer, defaultAppData),
  createReducerFunction(PersistImmerReducer, defaultAppData),
  createReducerFunction(ToolbarImmerReducer, defaultAppData),
  createReducerFunction(TopbarImmerReducer, defaultAppData),
  createReducerFunction(ChangeEditorModeImmerReducer, defaultAppData),
  createReducerFunction(MouseSelectionImmerReducer, defaultAppData),
  createReducerFunction(PaintImmerReducer, defaultAppData),
  byId
);
